const VipInfo = () => {
    return (
        <div>
            <section id="vip-benefit" className=" pt-5  justify-content-between">
                <h4>VIP benefits</h4>
                <ul>
                    <li>Can use get LP price command with @waterfall_help_bot</li>
                    <li>Get notifications about transactions on your wallet on both blockchains: Ethereum , BSC, Polygon, Fantom, Tomochain, Avalanche</li>
                </ul>
            </section>

            <section id="vip-fee" className=" pt-5  justify-content-between">
                <h4>VIP fee</h4>
                <ul>
                    <li>No charged fee</li>
                    <li>Just provide liquidity for WBNB-WBT on Pancakeswap and hold LP token</li>
                    <li>If you don't like VIP, remove liquidity and get your money back anytime</li>
                </ul>
            </section>

            <section id="become-vip" className=" pt-5  justify-content-between">
                <h4>How to become VIP (Hold WBNB-WBT LP token)</h4>
                <ul>
                    <li>Buy WBT token on Pancakeswap</li>
                    <li>Add liquidity for WBNB - WBT</li>
                </ul>
            </section>
        </div>
    );
}

const isMetaMaskInstalled = () => {
    //Have to check the ethereum binding on the window object to see if it's installed
    const { ethereum } = window;
    return Boolean(ethereum && ethereum.isMetaMask);
};
const handleNoMetamask = () => {
    setTimeout(function () {
        var win = window.open('http://metamask.io/', '_blank');
        alert('Please allow popups for this website');
        if (win) {
            //Browser has allowed it to be opened
            win.focus();
        }
    }, 5000);
}


const MetamaskClientCheck = async () => {
    let accs = await window.ethereum.request({ method: 'eth_requestAccounts' })
    return accs[0]
}

class VIP extends Component {
    state = { wallet: "", balance: 0, vip: false };
    async componentDidMount() {
        if (!isMetaMaskInstalled()) {
            return handleNoMetamask()
        } else {
            let w = await MetamaskClientCheck()
            this.setState({wallet: w})
        }

    }
    async onConnect () {
        if (!isMetaMaskInstalled()) {
            return handleNoMetamask()
        } else {
            let w = await MetamaskClientCheck()
            this.setState({wallet: w})
        }
    }
    render() {
        if (this.state.wallet && this.state.wallet != "") {
            if (!true) {
                return (
                    <>
                        <div className="pt-5  justify-content-between">
                            <div>
                                <span>Your wallet </span>
                                <span>{this.state.wallet.substr(0,5) + "..." + this.state.wallet.substr(this.state.wallet.length - 3)}</span>
                            </div>
                            <div>
                                <span>Balance </span>
                                <span>{this.state.balance} LP WBNB-WBT</span>
                            </div>
                            <div className="pt-5  justify-content-between">
                                <a  href="">Please BUY WBT and add liquidity to be VIP</a>
                            </div>
                        </div>
                        <VipInfo />
                    </>
                )
            } else {
                return (
                    <>
                        <div className="pt-5  justify-content-between">
                            <div>
                                <span>Your wallet </span>
                                <span>{this.state.wallet.substr(0,5) + "..." + this.state.wallet.substr(this.state.wallet.length - 3)}</span>
                            </div>
                            <div>
                                <span>Balance </span>
                                <span>{this.state.balance} LP WBNB-WBT</span>
                            </div>
                            <div className="pt-5  justify-content-between">
                                You are VIP. Please set your telegram ID to use VIP features
                            </div>
                            <div className="pt-5  justify-content-between">
                                <p>Please go to <a href="https://t.me/username_to_id_bot" rel="noopener noreferrer" target="_blank">https://t.me/username_to_id_bot</a></p>
                                <p>Put your username there to get your telegram ID</p>
                                 <input placeholder={"Your telegram ID"} value={""}/>
                                 <div className="pt-3  justify-content-between">
                                    <button id="btn-bar">Update</button>
                                 </div>
                            </div>
                            
                        </div>
                        <VipInfo />
                    </>
                )
            }

        } else {
            return (
                <>
                    <div className="pt-5  justify-content-between">
                        <button id="btn-bar" onClick={this.onConnect}>Connect wallet</button>
                    </div>
                    <VipInfo />
                </>
            )
        }


    }
}
export default VIP